.links {
  display: flex;
  margin-top: -4px;

  > a {
    width: 25%;
    text-overflow: ellipsis;
    overflow: hidden;
    padding: 4px;
    cursor: pointer;
    text-align: center;
    text-decoration: none;

    img {
      width: 100%;
    }

    span {
      white-space: nowrap;
      font-size: 12px;
      font-weight: bold;
      color: #8a8499;
    }
  }
}
